
.vux-fade-enter-active,.vux-fade-leave-active{opacity:1;transition:opacity linear .2s
}
.vux-fade-enter,.vux-fade-leave-to{opacity:0
}
.vux-dialog-enter-active,.vux-dialog-leave-active{opacity:1;transition-duration:.4s;transform:translate(-50%, -50%) scale(1) !important;transition-property:transform,opacity !important
}
.vux-dialog-enter,.vux-dialog-leave-to{opacity:0
}
.vux-dialog-enter{transform:translate(-50%, -50%) scale(1.185) !important
}
.vux-dialog-leave-to{transform:translate(-50%, -50%) scale(1) !important
}
.weui_mask{position:fixed;z-index:1000;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0.6)
}
.weui_mask_transparent{position:fixed;z-index:5001;width:100%;height:100%;top:0;left:0
}
.weui_mask_transition{display:none;position:fixed;z-index:1000;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0);transition:background .3s
}
.weui_fade_toggle{background:rgba(0,0,0,0.6)
}
.weui_dialog{position:fixed;z-index:5000;width:85%;top:50%;left:50%;transform:translate(-50%, -50%);background-color:#FAFAFC;text-align:center;border-radius:3px;overflow:hidden
}
.weui_dialog_confirm .weui_dialog .weui_dialog_hd{padding:1.2em 20px .5em
}
.weui_dialog_confirm .weui_dialog .weui_dialog_bd{text-align:left
}
.weui_dialog_hd{padding:1.2em 0 .5em
}
.weui_dialog_title{font-weight:400;font-size:17px
}
.weui_dialog_bd{padding:0 20px;font-size:15px;color:#888;word-wrap:break-word;word-break:break-all
}
.weui_dialog_ft{position:relative;line-height:42px;margin-top:20px;font-size:17px;display:-ms-flexbox;display:flex
}
.weui_dialog_ft a{display:block;-ms-flex:1;flex:1;color:#3CC51F;text-decoration:none;-webkit-tap-highlight-color:rgba(0,0,0,0)
}
.weui_dialog_ft a:active{background-color:#EEEEEE
}
.weui_dialog_ft:after{content:" ";position:absolute;left:0;top:0;width:100%;height:1px;border-top:1px solid #D5D5D6;color:#D5D5D6;transform-origin:0 0;transform:scaleY(.5)
}
.weui_dialog_confirm .weui_dialog_ft a{position:relative
}
.weui_dialog_confirm .weui_dialog_ft a:after{content:" ";position:absolute;left:0;top:0;width:1px;height:100%;border-left:1px solid #D5D5D6;color:#D5D5D6;transform-origin:0 0;transform:scaleX(.5)
}
.weui_dialog_confirm .weui_dialog_ft a:first-child:after{display:none
}
.weui_btn_dialog.default{color:#353535
}
.weui_btn_dialog.primary{color:#0BB20C
}
@media screen and (min-width:1024px){
.weui_dialog{width:35%
}
}


