
.weui_mask{position:fixed;z-index:1000;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0.6)
}
.weui_mask_transparent{position:fixed;z-index:5001;width:100%;height:100%;top:0;left:0
}
.weui_mask_transition{display:none;position:fixed;z-index:1000;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0);transition:background .3s
}
.weui_fade_toggle{background:rgba(0,0,0,0.6)
}
.weui_toast{position:fixed;z-index:50000;width:7.6em;min-height:7.6em;top:180px;left:50%;margin-left:-3.8em;background:rgba(40,40,40,0.75);text-align:center;border-radius:5px;color:#FFFFFF
}
.weui_icon_toast{margin:22px 0 0;display:block
}
.weui_icon_toast:before{content:'\EA08';color:#FFFFFF;font-size:55px
}
.weui_toast_content{margin:0 0 15px
}
.weui_loading_toast .weui_toast_content{margin-top:64%;font-size:14px
}
.weui_loading{position:absolute;width:0;z-index:1;left:50%;top:38%
}
.weui-loading{width:20px;height:20px;display:inline-block;vertical-align:middle;animation:weuiLoading 1s steps(12, end) infinite;background:transparent url(data:image/svg+xml;base64,) no-repeat;background-size:100%
}
@keyframes weuiLoading{
0%{transform:rotate3d(0, 0, 1, 0deg)
}
100%{transform:rotate3d(0, 0, 1, 360deg)
}
}
.weui-icon_toast.weui-loading{margin:30px 0 0;width:38px;height:38px;vertical-align:baseline
}
.weui_loading_toast .weui_toast_content{margin-top:0 !important
}
.weui-icon_toast.weui-loading{margin:30px 0 0;width:38px;height:38px;vertical-align:baseline
}


